"use client";

import { useEffect } from "react";
import { useLoading } from "../../../../context/LoadingContext";

export default function MyEventsList({ events = [] }) {
  // const { setLoading } = useLoading(); //ONLY ADD

  // useEffect(() => {
  //   setLoading(true); //START GLOBAL LOADING

  //   return () => {
  //     setLoading(false); //STOP GLOBAL LOADING
  //   };
  // }, [setLoading]);

  const hasEvents = Array.isArray(events) && events.length > 0;

  if (!hasEvents) {
    return (
      <div className="text-center py-5">
        <img
          src="/images/no-event-image.png"
          alt="No events"
          className="img-fluid mb-3"
          style={{ maxWidth: 300 }}
        />
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">
          Create your first event and unlock the full event-creator experience.
        </p>
      </div>
    );
  }

  return (
    <div className="list-group list-group-flush">
      {events.map((e) => {
        const location = [e?.org?.city, e?.org?.state, e?.org?.country]
          .filter(Boolean)
          .join(", ");

        const date = e?.createdAt
          ? new Date(e.createdAt).toLocaleDateString()
          : "";

        return (
          <div
            key={e.id}
            className="list-group-item border-0 mb-3 shadow-none p-3 mb-5 bg-body-tertiary rounded"
          >
            <div className="row align-items-center ">
              {/* IMAGE */}
              <div className="col-md-3">
                <img
                  src={e.bannerImage || "/images/event-placeholder.png"}
                  alt={e.title || "Event"}
                  className="img-fluid rounded-3"
                  style={{ height: 90, objectFit: "cover", width: "100%" }}
                />
              </div>

              {/* DETAILS */}
              <div className="col-md-6">
                <h6 className="fw-bold mb-1">
                  {e.title || "Untitled Event"}
                </h6>
                <p className="text-muted small mb-1">
                  {location || "Location not specified"}
                </p>
                <p className="text-muted small mb-0">{date}</p>
              </div>

              {/* ACTIONS */}
              <div className="col-md-3 text-md-end mt-3 mt-md-0">
                <span className="badge bg-primary me-2">{e.status}</span>
                <button className="btn btn-outline-primary btn-sm rounded-pill px-3">
                  Edit
                </button>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}
